import React from 'react';
import './About.css'

const About = () => {
	return (
			<div className="profile-card">
			  <div className="head">
			    <div className="profile-image"><img src="https://i.imgur.com/TUsGxjs.jpg" alt="me"/></div>
			    <div className="name-headline">
			      <h2>Student</h2>
			      <p>from</p>
			      <p><i className="fa fa-map-marker"></i> Timisoara, RO</p>
			    </div>
			  </div>
			  <div className="content">
			    <div className="about">I love things and stuffs. Need updates!</div>
			  </div>
			 </div>
		)
}

export default About;