import React, { Component } from "react";
import {
MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavItem, MDBNavLink, MDBNavbarToggler, MDBCollapse,
MDBDropdownItem} from "mdbreact";
import './Navigation.css';



class NavbarPage extends Component {
state = {
  isOpen: false
};

toggleCollapse = () => {
  this.setState({ isOpen: !this.state.isOpen });
}

render() {
  return (
    <div className="positie">
      <div className="myClass">
        <MDBNavbar dark expand="md" className="sticky-top">
          <MDBNavbarBrand>         
              <strong className="text">Angel TAT</strong>          
          </MDBNavbarBrand>
          <MDBNavbarToggler onClick={this.toggleCollapse} />
          <MDBCollapse id="navbarCollapse3" isOpen={this.state.isOpen} navbar>
            <MDBNavbarNav  right>
              <MDBNavItem >
                <MDBNavLink className="black-text" to="Home">Home</MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
                <MDBNavLink className="black-text" to="About">About</MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
                <MDBNavLink className="black-text" to="Projects">Projects</MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
              
                <MDBDropdownItem toggle={false} to="Resume" target="_blank" className="block-example border border-warning text2" href="https://drive.google.com/open?id=1S7FgYSl4rs1CgwC_vKp-H7VT8tIUvkhQ">Resume</MDBDropdownItem>
              
              </MDBNavItem>              
            </MDBNavbarNav>
          </MDBCollapse>
        </MDBNavbar>
      </div>
    </div>
    );
  }
}

export default NavbarPage;